import React, { useEffect } from "react"
import { navigate } from "gatsby"

const shows = [
  { picture: "Shinchan", text: "Shinchan", displayText: "Shinchan" },
]

const languages = ["Telugu", "Tamil", "English", "Hindi"]

const HomePage = () => {
  useEffect(() => {
    // Retrieve cookies for last viewed show and language
    const lastShow = document.cookie
      .split("; ")
      .find(row => row.startsWith("lastShow="))
    const lastLanguage = document.cookie
      .split("; ")
      .find(row => row.startsWith("selectedLanguage="))

    const show = lastShow
      ? lastShow.split("=")[1]
      : shows[0].text.toLowerCase().replace(/\s+/g, "-")
    const language = lastLanguage
      ? lastLanguage.split("=")[1]
      : languages[0].toLowerCase()

    // Redirect to the last viewed show and language, or the first in the list
    navigate(`/${show}/${language}`)
  }, [])

  return null // The component doesn't render anything visible
}

export default HomePage
